*
{
  margin: 0;
  padding: 0;
}
.app-input
{
  text-align: center;
  color: blue;

}
h1{
  color: green;
}
.app-input input{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid rgb(151, 3, 3);
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.app-input button{
  background-color: rgb(117, 7, 219);
  color: white;
  border: none;
  outline: none;
  margin-top: 10px;
  padding: 10px 30px;
  font-size: 15px;
  cursor: pointer;
}
nav{
  height:100vh;
  background:url("https://wallpapertag.com/wallpaper/full/d/3/c/968676-hi-res-background-images-2651x1813-retina.jpg") ;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: white;
  font-weight: bold;
}
nav ul li{
  cursor: pointer;
  font-size: 15px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.6);
} 
.navActive
{
  color: red;
  font-size: 20px;
  font-weight: bold;
}
.app-container{
  background-color: brown;
  font-size: 30px;
  color: white;
}